import React, {Component} from 'react';
import FirstPage from './firstPage';
import SecondPage from './secondPage';
import ThirdPage from './thirdPage';
import FourthPage from './fourthPage';
import '../scripts/script';
import '../css/main.css';
 



class Main extends Component {
	constructor(props) {
		super(props);

		this.state = {}
	}


componentDidUpdate() {
	 
}
openMenu() {
	 let slide_menu = document.querySelector('.slide_menu');
let hamb_icon = document.getElementById('hamb_icon');

	// display menu
	 slide_menu.classList.toggle('toggMenu');
	 // change hamb icon style
	hamb_icon.classList.toggle('open');
}

toggleMenuWork() {

document.querySelector('.menu_github').classList.toggle('toggleMenuWork');
	document.querySelector('.menu_portfolio').classList.toggle('toggleMenuWork');
}
contactMenu() {
 
	 

	document.querySelector('.m_soc_github').style.animation = 'github_bounce 0.5s';
	document.querySelector('.m_soc_linkedin').style.animation = 'linkedin_bounce 0.7s';
	document.querySelector('.m_soc_web').style.animation = 'm_soc_web_bounce 0.9s';
	document.querySelector('.menu_email_contact').style.opacity = '1';


	setTimeout(function() {
		document.querySelector('.m_soc_github').style.animation = 'none';
		document.querySelector('.m_soc_linkedin').style.animation = 'none';
		document.querySelector('.m_soc_web').style.animation = 'none';
	document.querySelector('.menu_email_contact').style.opacity = '0.6';

	 
	 
	},1000);
 
}
	render() {

		return	(
			<div>
				<div className='cursor'></div>

				<div className='loading_div'>

					<div className='row justify-content-center'>
					<span className='logo_backdrop'></span>
						<div className='load_logo_wrapper'>
							<span className='ball_load'></span>
							<div className='row justify-content-center'>
								<div className='llw_first'></div>
								<div className='llw_sec'>
								<span className='llw_sec_bubble'></span>
								</div>
							</div>
							<div className='row justify-content-center'>
								<div className='loading_txt'>
								<span className='loading_txt_dt' data-text='LOADING...'>

								</span>
								</div>
							</div>
						</div>
					</div>
				</div>


				<div className='slide_menu'>
					<div className='close_menu' onClick={() => this.openMenu()}>
		 					<span>&times;</span>
		 			</div>
		 			 

					<div className='row justify-content-center'>
						<div className='wrap_menu_list'>
							<a href='http://www.ionutdev.com'><p className='menu_about'>About</p></a>
							
							<p className='menu_work' onClick={() => this.toggleMenuWork()}>Work</p>

							
 							<p className='menu_github'>
 							<a href='https://github.com/SIonut0122'>
 							Github
 							</a>
 							</p>
 							

 						
 							<p className='menu_portfolio'>
 							<a href='http://www.ionutdev.com'>
 							Portfolio
							</a>
 							</p>
 							

							<p className='p_contact'
							 onClick={() => this.contactMenu()}>
							 <span></span>
							Contact
							</p>
						</div>
					</div>

					<div className='row justify-content-center'>
						<div className='menu_social_icons col-12 col-md-7 col-sm-9 col-xl-6 col-lg-6'>
							<div className='row justify-content-center'>
								<a href='https://github.com/SIonut0122'><span className='m_soc_github'></span></a>
								 <a href='https://www.linkedin.com/in/stan-ionut-1193a0159/'><span className='m_soc_linkedin'></span></a>
								<a href='http://www.ionutdev.com'><span className='m_soc_web'></span></a>
							</div>
							<div className='row justify-content-center'>
							<p className='menu_email_contact'>contact@ionutdev.com</p>
							</div>





						</div>
					</div>
				</div>

				<div className='swiper-container'>
					<div className='swiper-wrapper'>
					
		 			<div className='firstpage swiper-slide col-12'><FirstPage/></div>
		 	 
		 			<div className='secondpage swiper-slide col-12'><SecondPage/></div>	
		 	 
		 			<div className='thirdpage swiper-slide col-12'><ThirdPage/></div>

		 			<div className='fourthpage swiper-slide col-12'><FourthPage/></div>
		 			 
		 			</div>
		 		</div>

			</div>
		)
	}
}

export default Main;