import React, {Component} from 'react';
import '../css/firstPage/firstPage.css';
 


class FirstPage extends Component {
	constructor(props) {
		super(props);

			this.state = {}
	}


openMenu() {
	let hamb_icon = document.getElementById('hamb_icon');
 	let slide_menu = document.querySelector('.slide_menu');
 
 			
 			//toggle class hamb_menu icon and slide_menu's hamb
			hamb_icon.classList.toggle('open');
			// display menu
  			slide_menu.classList.toggle('toggMenu');

}



componentDidMount() {
	// load changing text when page loads	
	let txt = ['RESPONSIVE', 'INTERACTIVE', 'CREATIVE', 'RICH', 'BEAUTIFUL'];
	let txtDiv = document.querySelector('.descr_intro_txt');
	let counter = 0;

 
setTimeout(function() {
setInterval(function() {
	 
while (txtDiv.firstChild) {
    txtDiv.removeChild(txtDiv.firstChild);
}
	// split every letter from every word and push it to txtDiv to be animated

let split = txt[counter].split('');

	for(let i=0; i < split.length; i++) {

		let span = document.createElement('span');
		let letter = document.createTextNode(split[i]);

		span.appendChild(letter);
		txtDiv.appendChild(span);

	}
	
 counter++;

 if(counter >= txt.length) {
 	counter = 0;
 }
},1500);
 },1500);

 
}



leftHalfHover() {
	// display the video when hovering on the left half 
let firstHalfVid = document.getElementById('first_half_video');
let back_patr = document.querySelector('.back_patr');

let looking_intro_txt = document.querySelector('.looking_intro_txt');
let website_intro_txt = document.querySelector('.website_intro_txt');

     firstHalfVid.style.display = 'block';
	 firstHalfVid.play();
}

leftHalfHoverOut() {
	// hide the video when hovering on the left half 
let firstHalfVid = document.getElementById('first_half_video');
 
 	firstHalfVid.style.display = 'none';
 	firstHalfVid.pause();

}




rightHalfHover() {
	// display the video when hovering on the left half 
let rightHalfVid = document.getElementById('right_half_video');
 
	 rightHalfVid.style.display = 'block';
	 rightHalfVid.play();
}


rightHalfHoverOut() {
	// hide the video when hovering on the left half 
let rightHalfVid = document.getElementById('right_half_video');
 
 	 rightHalfVid.style.display = 'none';	
 	 rightHalfVid.pause();	
}

render() {
	return (
		<div>
			 
				<div className='firstpage_wrap container-fluid'>
		 			 


		 	{/* ------------ Left half ----------- */ }

					<div className='left_half' onMouseOver={() => this.leftHalfHover()}  onMouseLeave={() => this.leftHalfHoverOut()}>
						<div className='slide_lh_div'></div>	
								<video muted id='first_half_video'>
		    						<source src={require('../images/Light_Leak_Motion.mp4')} type='video/mp4'/>
							    </video>
					</div>

			{/* ------------ Right half ----------- */ }
					
					<div className='right_half' onMouseOver={() => this.rightHalfHover()}  onMouseLeave={() => this.rightHalfHoverOut()}>
						<div className='slide_rh_div'></div>
							<div className='row justify-content-center'>
								<video loop muted id='right_half_video'>
	    						<source src={require('../images/super_88.mov')} type='video/mp4'/>
						    </video>
						    </div>

						<div className='first_no_page'>
							<div className='row justify-content-center'>
							1
							</div>
							<div className='row justify-content-center'>
							<span></span>
							</div>
							<div className='row justify-content-center'>
							4
							</div>
						
						</div>
					</div>

			{/* ------------ Diamond shape ----------- */ }

					<div className='row justify-content-center'>
						<div className='back_patr'>
							<span className='back_patr_slide'></span>
							<span className='back_patr_slide_two'></span>
							<span className='back_patr_slide_three'></span>
							<span className='back_patr_slide_four'></span>

							<span className='dyn_line'></span>

						</div>

						<div className='back_patr_two'></div>
					</div>


		 				 
		 	{/* ------------ Navbar ----------- */ }

		 			<div className='logo_div'></div>

		 				<div className='hamb_menu'>
		 					<div id='hamb_icon' onClick={() => this.openMenu()}>
								<span className='h_first'></span>
								<span className='h_sec'></span>
								<span className='h_third'></span>
							</div>
		 				</div>

		 		 




		 		 


					<div className='row justify-content-center'>
		 				<div className='center_div'>
		 						<div className='row justify-content-center'>


		 						<div className='center_text_div col-12'>
		 							<div className='row justify-content-center'>
		 								<div className='looking_intro_txt'>Looking</div>
		 							</div>
		 							<div className='row justify-content-center'>
		 								<div className='forA'>for a</div>
		 							</div>
		 							
		 							<div className='row justify-content-center'>
		 								<div className='descr_intro_txt'>
		 									 <span>G</span>
		 									 <span>R</span>
		 									 <span>E</span>
		 									 <span>A</span>
		 									 <span>T</span>
		 									 
		 								</div>
		 							</div>
		 							<div className='row justify-content-center'>
		 								<span className='website_intro_txt'>website?</span>
		 							</div>
		 						</div>
		 					</div>

		 				



		 					</div>
		 				</div>


		 					<div className='row'> 
		 						<div className='anim_lines col-12'>
		 							<span className='first_anim_line'></span>
		 							<span className='second_anim_line'></span>
		 							<span className='third_anim_line'></span>
		 							<span className='fourth_anim_line'></span>
		 							 
		 					    </div>
		 					</div>

		 			<div className='row justify-content-center'>
		 				<div className='middle_line_div'>
		 					<div className='row justify-content-center'>
		 						<div className='midd_line'><span></span></div>
		 					</div>

		 					<div className='scroll_div'>
		 						<span className='scroll_div_line'></span>
		 						<span className='scroll_div_txt'>scroll</span>

		 					</div>
		 				</div>
		 			</div>



		 			<div className='right_contact_div'>
		 								<span className='right_contact_line'></span>
		 								<span className='right_contact_text'>Contact</span>
		 							</div>

		 	 
				 </div>

		</div>
		)
	}
}


export default FirstPage;