/* Background moving animation */

let lFollowX=0,
lFollowY=0,
x=0,
y=0,
friction=1 / 30;
function moveBackground() {
  x+=(lFollowX - x) * friction;
  y+=(lFollowY - y) * friction;
  let translate='translate('+x+'px, '+y+'px) scale(1.1)';
  $('.mov_bg').css( {
    '-webit-transform': translate, '-moz-transform': translate, 'transform': translate
  }
  );
  window.requestAnimationFrame(moveBackground);
}

$(window).on('mousemove click', function(e) {
  let lMouseX=Math.max(-100, Math.min(100, $(window).width() / 2 - e.clientX));
  let lMouseY=Math.max(-100, Math.min(100, $(window).height() / 2 - e.clientY));
  lFollowX=(20 * lMouseX) / 100; // 100 : 12 = lMouxeX : lFollow
  lFollowY=(10 * lMouseY) / 100;
}

);

// initiate function
moveBackground();
         const cursor = document.querySelector('.cursor');

        document.addEventListener('mousemove', e => {
            cursor.setAttribute("style", "top: "+(e.pageY - 10)+"px; left: "+(e.pageX - 10)+"px;")
        })




 
    
